
import React from 'react';
import {useRef, useState, useEffect} from 'react';
import styles from "./Overlay.module.css";
import Carousel from 'react-spring-3d-carousel';
import uuidv4 from "uuid";

export function Overlay({ playerChosen, scroll, setShowStemPlayer, setPlayerChosen }) {
    const scrollContainer = useRef();
    useEffect(() => {
        if (!playerChosen && scroll.current !== 0) {
            if (scrollContainer.current) {
                scrollContainer.current.scrollTo(0, scrollContainer.current.scrollHeight* scroll.current);
            }
        }
    }, [playerChosen]);

    const [state, setState] = useState({
      goToSlide: 0,
      offsetRadius: 3
    });

    const slides = [
      {
        key: uuidv4(),
        content:
        <div className={styles.caroItem}>
          <img className={styles.noImgSelector} src="/sp-1.png" alt="1" />
            <button
                onClick={() => {
                    setPlayerChosen(1);
                    setShowStemPlayer(1);
                }}
                className={styles.spButton + " pushable"}> SP-1</button>
          </div>
      },
      {
        key: uuidv4(),
        content:
        <div className={styles.caroItem}>
          <img className={styles.noImgSelector} src="/sp-2.png" alt="2" />
          <button
            onClick={() => {
                setPlayerChosen(2);
                setShowStemPlayer(2);
            }}
            className={styles.spButton + " pushable"}> SP-2</button>
          </div>
      },
      {
        key: uuidv4(),
        content:
        <div className={styles.caroItem}>
          <img className={styles.noImgSelector} src="/sp-3.png" alt="3" />
          <button
            onClick={() => {
                setPlayerChosen(3);
                setShowStemPlayer(3);
            }}
            className={styles.spButton + " pushable"}> SP-3</button>
        </div>
      },
      {
        key: uuidv4(),
        content:
        <div className={styles.caroItem}>
          <img className={styles.noImgSelector} src="/sp-4.png" alt="4" />
          <button
            onClick={() => {
                setPlayerChosen(4);
                setShowStemPlayer(4);
            }}
            className={"pushable " + styles.spButton}> SP-4</button>
        </div>
      },
      {
        key: uuidv4(),
        content:
        <div className={styles.caroItem}>
          <img className={styles.noImgSelector} src="/sp-5.png" alt="5" />
          <button
            onClick={() => {
                setPlayerChosen(5);
                setShowStemPlayer(5);
            }}
            className={"pushable " + styles.spButton}> SP-5</button>
        </div>
      },
      {
        key: uuidv4(),
        content:
        <div className={styles.caroItem}>
          <img className={styles.noImgSelector} src="/sp-6.png" alt="6" />
          <button
            onClick={() => {
                setPlayerChosen(6);
                setShowStemPlayer(6);
            }}
            className={"pushable " + styles.spButton}> SP-6</button>
        </div>
      }
    ].map((slide, index) => {
      return { ...slide, onClick: () => setState({ goToSlide: index }) };
    });

    const onChangeInput = (e) => {
      setState({
        [e.target.name]: parseInt(e.target.value, 10) || 0
      });
    };
  
    let xDown = null;
    let yDown = null;
  
    const getTouches = (evt) => {
      return (
        evt.touches || evt.originalEvent.touches // browser API
      ); // jQuery
    };
  
    const handleTouchStart = (evt) => {
      const firstTouch = getTouches(evt)[0];
      xDown = firstTouch.clientX;
      yDown = firstTouch.clientY;
    };
  
    const handleTouchMove = (evt) => {
      if (!xDown || !yDown) {
        return;
      }
  
      let xUp = evt.touches[0].clientX;
      let yUp = evt.touches[0].clientY;
  
      let xDiff = xDown - xUp;
      let yDiff = yDown - yUp;
  
      if (Math.abs(xDiff) > Math.abs(yDiff)) {
        /*most significant*/
        if (xDiff > 0) {
          /* left swipe */
          setState({ goToSlide: state.goToSlide + 1 });
        } else {
          /* right swipe */
          setState({ goToSlide: state.goToSlide - 1 });
        }
      } else {
        if (yDiff > 0) {
          /* up swipe */
        } else {
          /* down swipe */
        }
      }
      /* reset values */
      xDown = null;
      yDown = null;
    };

  return (
    <div
      ref={scrollContainer}
      className={styles.scrollContainer}
      onScroll={(e) => {
        const scroll01 =
          e.target.scrollTop / (e.target.scrollHeight - window.innerHeight);
        scroll.current = scroll01;
      }}
    >
      <section
        style={{ height: "50vh" }}
        className={styles.sectionLeft + " " + styles.sectionHero}
      >
        <header>
          <h1>
            <strong>SPORES</strong>.vision
          </h1>
          <svg
            width="15"
            height="30"
            viewBox="0 0 30 71"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <rect
              x="1.5"
              y="1.5"
              width="27"
              height="45"
              rx="13.5"
              stroke="#2D3A94"
              stroke-opacity="0.6"
              stroke-width="3"
            />
            <path
              d="M15 12V20"
              stroke="#2D3A94"
              stroke-opacity="0.6"
              stroke-width="3"
              stroke-linecap="round"
            />
            <path
              d="M8 54L14.5 60.5L21 54"
              stroke="#2D3A94"
              stroke-opacity="0.6"
              stroke-width="3"
              stroke-linecap="round"
              stroke-linejoin="round"
            />
            <path
              d="M8 63L14.5 69.5L21 63"
              stroke="#2D3A94"
              stroke-opacity="0.6"
              stroke-width="3"
              stroke-linecap="round"
              stroke-linejoin="round"
            />
          </svg>
        </header>
      </section>
      <section style={{ height: "180vh" }} className={styles.sectionRight + " " + styles.sectionFirst}>
        <p>
          Do you see it?
          <br />
          That light in the dark?
          <br />
          It calls to you.
        </p>
      </section>
      <section style={{ height: "50vh" }} className={styles.sectionLeft + " " + styles.sectionSecond}>
       <p>
          Deep underground...
        </p>
      </section>
      <section style={{ height: "150vh" }} className={styles.sectionRight + " " + styles.sectionLast}>
        <p>
          The spores resonate.
          <br />
          Awakening from their slumber.
        </p>
        <div className={styles.playerCarousel} onTouchStart={handleTouchStart} onTouchMove={handleTouchMove}>
        <Carousel slides={slides} goToSlide={state.goToSlide} offsetRadius={state.offsetRadius}/>
        </div>
        <div>
          <p className={styles.smallText}>
            Powered by
          </p>
          <img
            onClick={() => {
                window.location.href = "https://www.ghostwaveform.one";
            }
                    }
            src="/indigo-logo.png" style={{height: 30}} className={styles.logo + " pushable"}></img>
          <img src="/x.png" id={styles.x}></img>
          <img
            onClick={() => {
                window.location.href = "https://zequencer.io";
            }
                    }
            src="/zeq-logo.png" className={styles.logo + " pushable " + styles.zeqLogo}></img>
        </div>
      </section>
    </div>
  );
}
